import { autoinject, computedFrom } from "aurelia-framework";
import { Firebase } from "./firebase";
import { ISites } from "../interfaces/export";

var firebase;

@autoinject
export class User {
  constructor(private firebaseRef: Firebase) {

  }
  private itemsKey = "seedsAndSites";
  private authToken = null;
  private user = null;
  isLoggedIn = firebase.auth().currentUser.uid ? true : false;;

  @computedFrom("isLoggedIn")
  get userData(): any {
    let seedList: Array<ISites> = [];
    if (!this.isLoggedIn) {
      const defaultSeeds = this.firebaseRef.defaultSeeds;
      const localSeeds = localStorage.getItem(this.itemsKey);

      seedList = localSeeds ? JSON.parse(localSeeds) : defaultSeeds;
    } else {
      this.firebaseRef.getCustomSeedList(this.user.uid)
    }

    return {
      displayName: "",
      mail: "",
      photo: "",
      seedList: seedList
    }
  }
  login(type: string) {
    this.firebaseRef.login(type).then((result: any) => {
      // The token for this session
      this.authToken = result.credential.accessToken;

      // The user object containing information about the current user
      this.user = result.user;

      // Set a class variable to true to state we are logged in
      this.isLoggedIn = true;
    }).catch(error => {
      let errorCode = error.code;
      let errorMessage = error.message;
      let email = error.email;
      let credential = error.credential;

    });
  }
  logout() {
    this.firebaseRef.logout().then(() => {
      this.isLoggedIn = false;
    }).catch(error => {
      throw new Error(error);
    });;
  }
}